import React from "react"

import Layout from "../components/layout"
import { Link, useStaticQuery, graphql } from "gatsby"
import aesthetics from '../components/aesthetics.module.scss'

const BlogPage = () => {

    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark {
                edges {
                    node {
                        frontmatter {
                            title
                            date
                        }
                    }
                }
            }
        }
    `)

    return (
        <Layout>
            <h1>Blog</h1>
            <ol>
                {data.allMarkdownRemark.edges.map((edge) => {
                    return (
                        <li>
                            <Link className = {aesthetics.link} to = {`/blog/${edge.node.fields}`}>
                                <h2>{edge.node.frontmatter.title}</h2>
                                <p>{edge.node.frontmatter.date}</p>
                            </Link>
                        </li>
                    ) // NOTE FIX LINK TO BLOG -- IT DOESN'T WORK
                })}
            </ol>

            <p>lol these links dont work atm</p>
        </Layout>
    )

}

export default BlogPage